import {customElement, stringToHtml} from '@here/hds-base';
import {html, LitElement} from 'lit';
import {property} from 'lit/decorators.js';
import {classMap} from 'lit/directives/class-map.js';
import {styles} from './hds-logo.css.js';
import {HERE_LOGO} from './logo.svg.js';

export type LogoSize = 'extra-small' | 'small' | 'medium' | 'large' | 'extra-large';

@customElement('hds-logo')
export class Logo extends LitElement {
  static styles = styles;
  static shadowRootOptions = {...LitElement.shadowRootOptions, delegatesFocus: true};

  @property({type: Boolean}) inverted = false;
  @property({type: Boolean}) monochrome = false;
  @property({type: String}) size: LogoSize = 'extra-small';
  @property({type: Boolean}) padding = false;
  @property({type: Boolean}) background = false;

  static getSvgLogo() {
    return html`${stringToHtml(HERE_LOGO)}`;
  }

  protected render() {
    const logoClasses = {
      'hds-logo-wrapper': true,
      [`size-${this.size}`]: true,
      padded: this.background || this.padding,
      [this.inverted ? 'inverted' : 'non-inverted']: true,
      [this.monochrome ? 'monochrome' : 'non-monochrome']: true,
      [this.background ? 'has-background' : 'no-background']: true,
    };
    return html` <div class="${classMap(logoClasses)}">${Logo.getSvgLogo()}</div> `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-logo': Logo;
  }
}
